const DIRECTIONS = {
  LTR: 'ltr',
  RTL: 'rtl'
};
const getDirection = () => (typeof document !== 'undefined' && document.documentElement !== null && document.documentElement.getAttribute('dir') || DIRECTIONS.LTR).toLowerCase();
const isRTL = () => getDirection() === DIRECTIONS.RTL;
export default isRTL;